@import "../../Home/Mixins/Mixins.scss";
.checkout{
    background-color: white;
        // height: 100%;
        width: 100%;
        // position: fixed;
        position: absolute;
         left: 0;
        top: 0;
       z-index: 9999;
     padding-bottom: 100px;
       background: url('./../../../Assets/Brands/back1.jpg');
            background-repeat: no-repeat;
             background-size: 100% 100%;
            
     
    
    
    .checkout-main{
    display: flex;
    justify-content: center;
    gap: 50px;
    flex-flow: wrap;
    
        margin: 40px auto;
        padding: 40px 10px;
       
       
    
        @include md{
            padding: 40px 30px;
       
        }
    
        .checkout-container {
            // max-width: 800px;
            width: 600px;
          
            padding: 40px 10px;
            border: 1px solid #ddd;
            background-color: white;
       
            border-radius: 8px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        
            @include md{
                padding: 40px 20px;
            }
    
            @include formp{
    width: 100%;
            }
            .formh{
                text-align: center;
                color: gray;
            }
          
        
        }
    
        .formh{
            font-size: 25px;
        }
        .checkout-price {
      width: 100%;
            height:fit-content;
            padding: 5px 10px;
            line-height: 50px;
            font-size: 25px;
            background-color: white;
    
            @include md{
                min-width: 400px;
                padding: 5px 30px;
                max-width: 300px;
            }
    
        
          
           
            border: 1px solid #ddd;
            // background-color: rgb(231, 227, 235);
            border-radius: 8px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    
            h1{
                // border-bottom: 1px solid #ddd;
                color: rgba(0, 0, 0, 0.6);
                padding: 8px 0;
            }
    
            .total-value{
    
                font-size: 16px;
                display: flex;
                justify-content: space-between;
                
    
                span{
                    &:nth-child(1){
                        font-weight: 900;
                        font-size: 17px;
                       
                    }
        
                    &:nth-child(2){
                       text-align: right;
                        font-size: 19px;
                        color: lightblue;
                      
                    }
                }
    
            
    
                
            }
    
            .delivery-charge{
                // border-bottom: 1px solid #ddd;
    
                font-size: 16px;
                display: flex;
                justify-content: space-between;
    
                
    
                span{
                    &:nth-child(1){
                        font-weight: 900;
                        font-size: 17px;
                       
                    }
        
                    &:nth-child(2){
                       text-align: right;
                        font-size: 19px;
                        color: lightblue;
                      
                    }
                }
    
            
    
                
            }
    
            .paid-charge{
    
                font-size: 16px;
                display: flex;
                justify-content: space-between;
                
    
                span{
                    &:nth-child(1){
                        font-weight: 900;
                        font-size: 22px;
                        color: rgb(62, 131, 235);
    
                    }
        
                    &:nth-child(2){
                       text-align: right;
                        font-size: 22px;
                        color: green;
                        font-weight: 900;
                      
                    }
                }
    
            
    
                
            }
    
    
            
        
          
          
        
        }
    }
    
    }