@import "../../Mixins/Mixins.scss";
.shipping-form {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px 10px;
    // border: 1px solid #ddd;
    border-radius: 8px;
    // box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 40px;

    @include md{
        padding: 20px;
    }

    .h5{
        margin-bottom: 20px;
    }

    form{
        display: flex;
        flex-direction: column;
        gap: 20px;

        .row1{
            display: flex;
            flex-direction: column;
            gap: 20px;
          
          

           @include md{
            display: flex;
             justify-content: space-between;
             flex-direction: row;

           }
        }

      

        .btn{
            width: fit-content;
          align-self: flex-end;

        }
    }

  }