
.register-container {
    width: 400px;
    padding: 20px;
    margin: auto;
    height: 600px;
    
  }
  // .main{
  //   // background-color: #9c9ea0;
  // }
  
  .register-container h2 {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .register-form {
    // background-color: #9c9ea0;
    border: 1px solid black;
    border-radius: 5px;
    padding: 20px;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 1); 
    
  }
  
  .register-field {
    margin-bottom: 15px;
  }
  
  .register-field input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .error-message {
    color: red;
    margin-top: 5px;
  }
  
